footer{
    width: 100%;
    height: 344px;
    background: #222222;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .logo{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .bottom-nav-bar ul{
        width: 490px;
        margin: 54px 0;
        display: flex;
        justify-content: space-around;
        list-style-type: none;

        .nav-link a{
            text-decoration: none;
            font-family: Gilroy;
            font-size: 16px;
            line-height: 16px;
            letter-spacing: 3px;
            text-transform: uppercase;
            color: #777777;

            &:hover{
                color: #ffffff;
            }
        }
    }

    .footer-text{
        width: 253px;
        height: 24px;
        font-family: Poppins;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 24px;
        text-align: center;
        letter-spacing: 0.2px;
        color: #777777;
    }
}